export default(state,action)=>{
    switch(action.type){
        case 'DELETE_TRANSACTION':
            return{
                ...state,
                trasnactions: state.trasnactions.filter(trasnaction => trasnaction.id !== 
                    action.payload)
            }
            case 'ADD_TRANSACTION':
                return{
                    ...state,
                    trasnactions:[action.payload, ...state.trasnactions]
                }
        default:
            return state;
    }
}


